<template>
  <md-layout md-column v-on:click="onClick">
    <toolbar></toolbar>
    <note-form :notes="notes" :on_edit="on_edit"></note-form>
    <md-layout>
      <notes :notes="notes"></notes>
    </md-layout>
  </md-layout>
</template>

<script>
  import toolbar from './components/toolbar'
  import notes from './components/notes'
  import noteForm from './components/noteForm'

  export default {
    name: 'app',
    data () {
      return {
        notes: [
          {
            id: "0",
            type: "ordinary",
            title: "灵感",
            content: "在线memo可以记下创意",
            images: ["/images/lunch.png"]
          },
          {
            id: "1",
            type: "list",
            lists: [
              {content: "起床", done: false},
              {content: "刷牙", done: false},
              {content: "吃饭", done: false}
            ],
            title: "功能",
            content: "设置网页提醒,以及到达率较高的短信提醒，随时随地",
            images: []
          },
          {
            id: "2",
            type: "ordinary",
            title: "多彩",
            content: "支持导入图片，链接"
          },
          {
            id: "3",
            type: "ordinary",
            title: "智",
            content: "将接入语意识别，使用更加智能"
          }
        ],
        newNote: {},
        on_edit: false
      }
    },
    components: {
      'toolbar': toolbar,
      'noteForm': noteForm,
      'notes': notes
    },
    methods: {
      onClick() {
        this.on_edit = false;
      }
    },
    mounted() {

    }
  }
</script>

<style scoped>
</style>



