<template>
  <md-card md-with-hover @click.native="openDialog('detail')"
           @mouseover.native="showMenu"

           style="max-width: 400px; margin: 4px;background-color: #fafafa" draggable="true">
    <md-card-header>
      <div class="md-title" v-html="note.title"></div>
    </md-card-header>

    <!--<md-card-media>-->
    <!--<img src="assets/card-image-1.jpg" alt="People">-->
    <!--</md-card-media>-->

    <md-card-content v-html="note.content"></md-card-content>

    <!--<md-card-actions>-->
    <!--<md-button class="md-primary md-raised">Detail</md-button>-->
    <!--<md-button>Action</md-button>-->
    <!--</md-card-actions>-->

    <md-dialog
      @open="onOpen"
      @close="onClose"
      ref="detail">
      <edit-note :note="note"
                 :on_edit="true"
      ></edit-note>
    </md-dialog>
  </md-card>
</template>
<script>
  import editNote from "./editNote"
  export default {
    data () {
      return {}
    },
    methods: {
      openDialog(ref) {
        this.$refs[ref].open();
      },
      closeDialog(ref) {
        this.$refs[ref].close();
      },
      onOpen() {
        console.log('Opened');
      },
      onClose(type) {
        console.log('Closed', type);
      },
      showMenu() {
//          console.log("you should show menu");
      },
      dragStart(e) {
        console.log(e)
      },
      dragEnter(e) {
        console.log("drag enter");
        console.log(e.target);
      },
      dropEnd(e) {
        console.log("drop here");
        console.log(e);
      }
    },
    props: ["note", "notes"],
    components: {
      "editNote": editNote,
    }
  }
</script>
<style scoped>

</style>



