<template>
  <md-layout md-align="center">
    <div class="md-card" style="width: 600px; margin: 13px; display: flex; outline: none;background-color: #fafafa"
         contenteditable="false"
         v-on:click.capture="onClick">
      <div contenteditable="true" style="outline: none; font-size: large" placeholder="标题" ref="title"></div>
      <div contenteditable="true" style="outline: none;" placeholder="添加内容..." ref="content"></div>
      <div contenteditable="false" style="padding-left: 590px;">
      </div>
      <transition name="tool">
        <md-layout v-if="on_edit">
          <md-layout md-flex="80">
            <md-button>
              <md-icon>
                photo
              </md-icon>
              <md-tooltip md-direction="bottom">添加图片</md-tooltip>
            </md-button>
            <md-button>
              <md-icon>
                share
              </md-icon>
              <md-tooltip md-direction="bottom">分享</md-tooltip>
            </md-button>
            <md-button>
              <md-icon>
                format_color_text
              </md-icon>
              <md-tooltip md-direction="bottom">颜色</md-tooltip>
            </md-button>
          </md-layout>

          <md-layout md-align="end">
            <md-button md-fab md-fab-bottom-right @click.native="addNote" class="md-primary">
              完成
            </md-button>
          </md-layout>


        </md-layout>
      </transition>
    </div>
  </md-layout>
</template>
<script>
  import $ from "jquery"
  export default {
    data () {
      return {}
    },
    methods: {
      onFocus() {
//        console.log("focus on input form")
      },
      onBlur() {
//        console.log("blur on input form")
      },
      onClick() {
//        console.log("click form");
        this.on_edit = true;
      },
      addNote() {
        console.log(this.$refs.title.innerHTML);
        console.log(this.$refs.content.innerHTML);
        if (this.$refs.title.innerHTML === "" && this.$refs.content.innerHTML === "") {
          return
        }
        this.notes.push({
          id: this.notes.count + 1,
          type: "ordinary",
          title: this.$refs.title.innerHTML,
          content: this.$refs.content.innerHTML,
          images: ["/images/lunch.png"]
        });
        this.$refs.title.innerHTML = '';
        this.$refs.content.innerHTML = '';
      }
    },
    props: ["notes", "on_edit"]
  }
</script>
<style scoped>
  [contenteditable=true]:empty:before {
    content: attr(placeholder);
    display: block; /* For Firefox */
  }

  /* */

  div[contenteditable=true] {
    /*border: 1px dashed #AAA;*/
    /*width: 290px;*/
    padding: 5px;
  }

  pre {
    background: #EEE;
    padding: 5px;
    /*width: 290px;*/
  }

  .tool-enter-active {
    transition: opacity .7s
  }

  .tool-leave-active {
    transition: opacity 0s
  }

  .tool-enter, .tool-leave-active {
    opacity: 0
  }
</style>



