<template>
  <md-layout md-align="center">
    <draggable v-model="notes">
      <div v-for="note in notes" style="display: inline-block">
        <note :note="note" :notes="notes"></note>
      </div>
    </draggable>
  </md-layout>
</template>
<script>
  import note from './note'
  import draggable from 'vuedraggable'

  export default {
    data () {
      return {}
    },
    methods: {

    },
    props: ["notes"],
    components: {
      "note": note,
      "draggable": draggable
    }
  }
</script>
<style scoped>

</style>



